import AbstractView from '../framework/view/abstract-view.js';

function createTripFiltersTemplate() {
  return (
    `<div class="trip-filters__filter">
      <input id="filter-everything"
      class="trip-filters__filter-input visually-hidden"
      type="radio"
      name="trip-filter"
      value="everything"/>
      <label class="trip-filters__filter-label" for="filter-everything">Everything</label>
    </div>
    <div class="trip-filters__filter">
      <input id="filter-future" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="future" />
      <label class="trip-filters__filter-label" for="filter-future">Future</label>
    </div>
    <div class="trip-filters__filter">
      <input id="filter-present" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="present" />
      <label class="trip-filters__filter-label" for="filter-present">Present</label>
    </div>
    <div class="trip-filters__filter">
      <input id="filter-past" class="trip-filters__filter-input  visually-hidden" type="radio" name="trip-filter" value="past" checked />
      <label class="trip-filters__filter-label" for="filter-past">Past</label>
    </div>`
  );
}

function createFilterFormTemplate() {
  return (
    `<form class="trip-filters" action="#" method="get">
      ${createTripFiltersTemplate()}
      <button class="visually-hidden" type="submit">Accept filter</button>
    </form>`
  );
}

export default class ListFiltersView extends AbstractView {
  get template() {
    return createFilterFormTemplate();
  }
}
